/* ubuntu-300 - latin_cyrillic */
@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 300;
  src: local("Ubuntu Light"), local("Ubuntu-Light"), url("../fonts/ubuntu-v11-latin_cyrillic-300.woff2") format("woff2"), url("../fonts/ubuntu-v11-latin_cyrillic-300.woff") format("woff");
}

/* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */

/* ubuntu-regular - latin_cyrillic */
@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 400;
