html,
body {
  font-family: "Ubuntu", Arial, Helvetica, sans-serif;
  font-size: 19px;
  line-height: 1.57894737;
  color: #234;
  background-color: #eee;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;

  &.noscroll {
    overflow: hidden;
  }
}

section {
  padding: 32px 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", Arial, sans-serif;
  font-weight: normal;
  text-align: center;
  &.hidden {
    display: none;
  }
}

h1 {
  font-size: 60px;
  margin-top: 40px;
}

h2 {
  font-size: 50px;
  margin-top: 32px;
}

h3,
.h3 {
  font-size: 40px;
  margin-top: 24px;
}

p {
  margin: 0 0 16px;
}

a {
  color: $dark_green;
  text-decoration: none;

  &:visited {
    color: $dark_green;
  }

  &:hover,
  &:active {
    color: #84a580;
    text-decoration: none;
  }
}

.alert_box {
  position: fixed;
  z-index: 1001;
  width: 80%;
  left: 10%;
  top: 10%;
  opacity: 0.9;
}

.alert {
  text-align: center;
}

.button, a.button {
  text-transform: uppercase;
  padding: 8px 16px;
  border-radius: 2px;
  display: inline-block;
  background: transparent;
}

.button:hover, a.button:hover {
  color: white;
  transition: 0.5s;
}

.white_button, a.white_button {
  color: white;
  border: 2px solid white;
}

.white_button:hover, a.white_button:hover {
  color: #234;
  background: white;
}

.light_button, a.light_button {
  color: #84a580;
  border: 2px solid #84a580;
}

.light_button:hover, a.light_button:hover {
  background: #84a580;
}

.dark_button, a.dark_button {
  color: $dark_green;
  border: 2px solid $dark_green;
}

.dark_button:hover, a.dark_button:hover {
  background: $dark_green;
}

.red_button, a.red_button {
  color: #a94442;
  border: 2px solid #a94442;
}

.red_button:hover, a.red_button:hover {
  background: #a94442;
}

.center {
  text-align: center;
}

.error_page {
  padding: 64px;
}

@media screen and (max-width: 1366px) {}

@media screen and (max-width: 1024px) {
  h2 {
    font-size: 40px;
    margin-top: 24px;
  }

  h3 {
    font-size: 32px;
    margin-top: 16px;
  }
}

@media screen and (max-width: 768px) {}

@media screen and (max-width: 414px) {}

@media screen and (max-width: 375px) {
  h2,
  h3 {
    font-size: 24px;
    margin-top: 16px;
  }
}

@media screen and (max-width: 320px) {}
